.post{
    width: 385px;
    margin: 0px 25px 40px 25px;
}
@media screen and (max-width: 1250px){
    .post{
        width: 85%;
    }
}

.postImg{
    width: 100%;
    height: 280px;
    object-fit: cover;
    border-radius: 5px;
    margin-top: 15px;
}

.postInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.postCat{
    font-family: 'Source Sans Pro', sans-serif;
    font-size: 11px;
    color: #C36C32;
    line-height: 20px;
    margin-top: 15px;
    margin-right: 10px;
}

.postTitle{
    font-family: 'Playfair Display', serif;
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}

.postDate{
    font-family: 'Source Sans Pro', sans-serif;
    font-style: italic;
    font-size: 13px;
}

.postDesc{
    font-size: 14px;
    line-height: 24px;
    margin-top: 15px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
}